<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="accountTableData" :noDataText="accountTableNoDataText" @selected="selectRow" search>
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="refreshAccountTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="accountTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ accountTablePagingInfo }}</span>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="nickname">
            {{ this.$t('ACCOUNT_TITLE') }}
          </vs-th>
          <vs-th sort-key="accountNumber">
            {{ this.$t('ACCOUNT_NUMBER') }}
          </vs-th>
          <vs-th sort-key="iban">
            {{ this.$t('IBAN') }}
          </vs-th>
          <vs-th sort-key="typeName">
            {{ this.$t('ACCOUNT_TYPE') }}
          </vs-th>
          <vs-th sort-key="currencyAbbreviation">
            {{ this.$t('CURRENCY') }}
          </vs-th>
          <vs-th sort-key="availableBalance">
            {{ this.$t('AVAILABLE_BALANCE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].nickname">
              {{ data[index].nickname | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].accountNumber">
              {{ data[index].accountNumber | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].iban">
              {{ data[index].iban | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].typeName">
              {{ data[index].typeName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].currencyAbbreviation">
              {{ data[index].currencyAbbreviation | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].availableBalance">
            <span :class="negativeNumberStyle(data[index].availableBalance)">
              {{ data[index].availableBalance | currency('') | placeholder('-') }}
            </span>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import AccountTableMixin from '@/mixins/account/AccountTableMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'account-popup',
  mixins: [AccountTableMixin, NumberStyleMixin, PopupMixin],
  mounted: function () {
    this.loadAccountTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
