let LookupMixin = {
  props: {
    suggestRef: {
      type: String,
      default: 'suggest'
    },
    popupRef: {
      type: String,
      default: 'popup'
    },
    lookupTag: {
      type: Number,
      default: null
    },
    title: {
      type: String,
      default: null
    },
    displayField: {
      type: String,
      required: true
    },
    valueField: {
      type: String,
      required: true
    },
    recordValue: {
      type: String,
      default: null
    },
    recordDisplay: {
      type: String,
      default: null
    },
    defaultTableFilter: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      popupActive: false,
      loading: false,
      mutableRecordValue: this.recordValue,
      mutableRecordDisplay: this.recordDisplay
    }
  },
  methods: {
    boldenSuggestion (scope) {
      if (!scope) return scope
      const { suggestion, query } = scope
      let result = this.$refs[this.suggestRef].displayProperty(suggestion)
      if (!result) return scope
      if (!query) return result
      const texts = query.split(/[\s-_/\\|.]/gm).filter(t => !!t) || ['']
      return result.replace(new RegExp('(.*?)(' + texts.join('|') + ')(.*?)', 'gi'), '$1<b>$2</b>$3')
    },
    selectRow (row) {
      this.mutableRecordValue = row[this.valueField]
      this.mutableRecordDisplay = row[this.displayField]
      this.$refs[this.suggestRef].clearSuggestions()
      this.$emit('select-record', row, this.lookupTag)
    },
    selectSuggestion (suggestion) {
      if (suggestion) {
        this.mutableRecordValue = suggestion[this.valueField]
        this.mutableRecordDisplay = suggestion[this.displayField]
        this.$emit('select-record', suggestion, this.lookupTag)
      }
    },
    clear () {
      this.mutableRecordValue = ''
      this.mutableRecordDisplay = ''
      this.$refs[this.suggestRef].clearSuggestions()
      this.$emit('select-record', {}, this.lookupTag)
    }
  },
  watch: {
    recordValue: function (val) {
      this.mutableRecordValue = val
    },
    recordDisplay: function (val) {
      this.mutableRecordDisplay = val
    }
  }
}

export default LookupMixin
